export default {
	name:'one',
	data() {
		return {
			data:{},
			Dat:{
				name:'',
				id:'',
				lei:'',
				time:''
			},
			itemList:['维修', '安装', '保养'],
			codeurlids:"",//判断进来的状态
			textareaAValue:''//处理任务备注
		}
	},
	mounted() {
		uni.showLoading({
		    title: '加载中'
		});
		this.codeurlids = this.$store.state.codeurlids
		console.log(this.$store.state.codeurlids)
		uni.request({
		    url: this.$store.state.url + 'WO/GetWorkOrderDetailFE',
		    method: 'GET',
		    data: {
		       woId :this.$store.state.codeurlid.WorkOrderId
		    },
		    header: {
		        'Token': this.$store.state.token
		    },
		    success: (res) => {
		        this.data = JSON.parse(res.data).Data
				console.log(this.data)
				setTimeout(function () {
				    uni.hideLoading();
				}, 500);
		    }
		});
	},
	methods: {
		gcsapi(){
			uni.request({
			    url: this.$store.state.url + 'System/GetEmployeeMini',
			    method: 'GET',
			    data: {
			       type  : '1'
			    },
			    header: {
			        'Token': this.$store.state.token
			    },
			    success: (res) => {
					var asd = []
					for(var i = 0; i < JSON.parse(res.data).Data.length; i ++){asd.push(JSON.parse(res.data).Data[i].Name)}
					console.log(JSON.parse(res.data).Data)
					uni.showActionSheet({
					    title:'选择工程师',
					    itemList: asd,
					    success: (e) => {
							this.Dat.name = asd[e.tapIndex]
							this.Dat.id = JSON.parse(res.data).Data[e.tapIndex].EmployeeId
					        uni.showToast({
					            title:"点击了第" + e.tapIndex + "个选项",
					            icon:"none"
					        })
					    }
					})
					
					
					
					
			    }
			});
		},
		type(){
			uni.showActionSheet({
			    title:'选择类型',
			    itemList: ['维修', '安装', '保养'],
			    success: (e) => {
					this.Dat.lei = this.itemList[e.tapIndex]
			        uni.showToast({
			            title:"点击了第" + e.tapIndex + "个选项",
			            icon:"none"
			        })
			    }
			})
		},
		//提交按钮
		but(){
			if(this.Dat.id == ''){
				uni.showToast({
				    title:"请选择工程师",
				    icon:"none"
				})
			}else if(this.Dat.lei == ''){
				uni.showToast({
				    title:"请选择类型",
				    icon:"none"
				})
			}else if(this.Dat.time == ''){
				uni.showToast({
				    title:"请输入工时",
				    icon:"none"
				})
			}else{
				
				 uni.request({
				        url: this.$store.state.url + 'WO/AssignTask',
				        method: 'POST',
				        data: {
				            EngineerId  : this.Dat.id,
				            RepairType : this.Dat.lei,
				            RepairTime : this.Dat.time,
				            WoId : this.$store.state.codeurlid.WorkOrderId,
				        },
				        header: {
				            'Token': this.$store.state.token
				        },
				        success: (res) => {
				            uni.navigateTo({
				                    url: "../work.1",
				                });
				        }
				    });

			}
		},
		//接受按钮
		primary(){
			uni.request({
			       url: this.$store.state.url + 'WO/ConfirmAssign',
			       method: 'POST',
			       data: {
			           WoId : this.$store.state.codeurlid.WorkOrderId,
					   Accept : true
			       },
			       header: {
			           'Token': this.$store.state.token
			       },
			       success: (res) => {
			           uni.navigateTo({
			                   url: "../work.1",
			               });
			       }
			   });	
		},
		//拒绝
		warn(){
			uni.request({
			       url: this.$store.state.url + 'WO/ConfirmAssign',
			       method: 'POST',
			       data: {
			           WoId : this.$store.state.codeurlid.WorkOrderId,
					   Accept : false
			       },
			       header: {
			           'Token': this.$store.state.token
			       },
			       success: (res) => {
			           uni.navigateTo({
			                   url: "../work.1",
			               });
			       }
			   });
		},
		//处理完成
		wnac(){
			uni.request({
			       url: this.$store.state.url + 'WO/ProcessingTask',
			       method: 'POST',
			       data: {
			           WoId : this.$store.state.codeurlid.WorkOrderId,
					   RepairRemark  : this.textareaAValue
			       },
			       header: {
			           'Token': this.$store.state.token
			       },
			       success: (res) => {
			           uni.navigateTo({
			                   url: "../work.1",
			               });
			       }
			   });
		},
		textareaBInput(e) {
			this.textareaAValue = e.detail.value
		},
		
	}
}
